import { waitForSeconds } from '../../helpers/asyncHelper';
import { accountDatabase } from './accountDatabase';
import {
    getLoggedInEmail,
    getManagedUser,
    getManagedUserEmail,
    setLoggedInEmail,
    setManagedUser,
    setManagedUserEmail,
} from '../sessionProps';
import { passwordDatabase } from './passwordDatabase';

/* eslint-disable import/prefer-default-export */
export const AuthServiceMock = {
    // Public interface
    
    async getLoggedInEmail() {
        return getLoggedInEmail();
    },

    async getManagedUser() {
        return getManagedUser();
    },

    async getManagedUserEmail() {
        return getManagedUserEmail();
    },

    async isManagingUser() {
        return getManagedUser() !== null;
    },

    async manageUser(key) {
        // Faking a server response time.
        await waitForSeconds(0.1 + Math.random() * 0.2);

        setManagedUser(key);
        setManagedUserEmail(accountDatabase[key].email);
        return true;
    },

    async getLoggedInKey() {
        const loggedInEmail = getLoggedInEmail();
        const account = Object.keys(accountDatabase).find(key => accountDatabase[key].email === loggedInEmail);
        return account || null;
    },

    async getRole() {
        const loggedInEmail = getLoggedInEmail();
        if (loggedInEmail && passwordDatabase[loggedInEmail]) {
            return passwordDatabase[loggedInEmail].role;
        }

        return null;
    },

    // eslint-disable-next-line no-unused-vars
    async logIn(email, password) {
        // Faking a server response time.
        await waitForSeconds(0.1 + Math.random() * 0.2);

        if (passwordDatabase[email]) {
            setLoggedInEmail(email);
        } else {
            setLoggedInEmail(null);
        }

        setManagedUser(null);
        setManagedUserEmail(null);

        return !!getLoggedInEmail();
    },

    async logOut() {
        await waitForSeconds(0.1 + Math.random() * 0.2);

        setLoggedInEmail(null);
        setManagedUser(null);
        setManagedUserEmail(null);
    },
};
