import axios from 'axios';
import {
    getLoggedInEmail,
    getManagedUser,
    getManagedUserEmail,
    setLoggedInEmail,
    setManagedUser,
    setManagedUserEmail,
    getRole,
    setRole,
    getLoggedInKey,
} from './sessionProps';
import { API_URL } from './api';
import { waitForSeconds } from '../helpers/asyncHelper';
import { accountDatabase } from './mock/accountDatabase';
import { USER, ADMIN, SUBUSER } from '../types/role.types';
import { AuthServiceMock } from './mock/authServiceMock';

export const AuthService = {
    // Public interface
    
    async getRole() {
        return getRole();
    },

    async getLoggedInEmail() {
        return getLoggedInEmail();
    },

    async getManagedUser() {
        return getManagedUser();
    },

    async getManagedUserEmail() {
        return getManagedUserEmail();
    },

    async isManagingUser() {
        return getManagedUser() !== null;
    },

    async getLoggedInKey() {
        return getLoggedInKey();
    },

    async manageUser(key) {
        // Faking a server response time.
        await waitForSeconds(0.1 + Math.random() * 0.2);

        setManagedUser(key);
        setManagedUserEmail(accountDatabase[key].email);
        return true;
    },

    // eslint-disable-next-line no-unused-vars
    async logIn(email, password) {
        setManagedUser(null);
        setManagedUserEmail(null);

        try {
            const response = await axios.get(`${API_URL}/authentication_token`, {
                params: {
                    email,
                    password,
                },
            });
            
            const { roles } = response.data;

            setLoggedInEmail(email);

            if (roles) {
                if (roles.includes('ROLE_USER')) setRole(USER);
                if (roles.includes('ROLE_SUBUSER')) setRole(SUBUSER);
                if (roles.includes('ROLE_ADMIN')) setRole(ADMIN);
            }
    
            return true;
        } catch (e) {
            console.error(e);
        }
        
        setLoggedInEmail(null);

        return false;
    },

    async logOut() {
        await waitForSeconds(0.1 + Math.random() * 0.2);

        setLoggedInEmail(null);
        setManagedUser(null);
        setManagedUserEmail(null);
    },
};


/* eslint-disable import/prefer-default-export */
export function getAuthService() {
    return AuthServiceMock;
}
