<template>
<div class="button-wrapper">
    <button
            class="button"
            type="button"
            :class="computedBorderColorClass"
            @click="click"
    >
        {{text}}
    </button>
</div>
</template>

<script>
export default {
    name: 'Button',
    props: {
        text: {
            type: String,
            default: null,
            required: true,
        },
        borderColor: {
            type: String,
            default: 'white',
            required: true,
        },
    },
    computed: {
        computedBorderColorClass() {
            let borderColorClass = '';

            if (this.borderColor === 'white') {
                borderColorClass = 'border-white';
            } else if (this.borderColor === 'primary') {
                borderColorClass = 'border-primary';
            } else {
                borderColorClass = 'border-white';
            }

            return borderColorClass;
        },
    },
    methods: {
        click() {
            this.$emit('click');
        },
    },
};
</script>

<style lang="scss" scoped>
@import '@/assets/scss/_variables.scss';
@import "@/assets/scss/bulma/projectBulma.scss";

.button-wrapper {
    width: 100%;

    .button {
        background-color: $black;
        border: solid 2px $primary;
        color: $white;
        width: 100%;
        min-width: 85px;

        &.border-white {
            border-color: $white;
        }

        &.border-primary {
            border-color: $primary;
        }
    }
}

