<template>
    <div class="main-page-header-wrapper">
        <nav class="navbar is-black" role="navigation" aria-label="main navigation">
            <div class="navbar-brand">
                <a class="navbar-item logo" href="/">
                    <img src="@/assets/images/logo.png">
                </a>

                <a role="button" class="navbar-burger burger"
                v-bind:class="{'is-active': navbarBurgerExpanded}"
                @click="expandNavbarBurger"
                aria-label="menu" aria-expanded="false"
                data-target="navbarBasicExample">
                    <span aria-hidden="true"></span>
                    <span aria-hidden="true"></span>
                    <span aria-hidden="true"></span>
                </a>
            </div>

            <div id="navbarBasicExample" class="navbar-menu is-black"
                v-bind:class="{'is-active': navbarBurgerExpanded}">
                <div class="navbar-start">
                    <router-link to="/" class="navbar-item">
                        Home
                    </router-link>
                    
                    <div class="navbar-item has-dropdown is-hoverable">
                        <a class="navbar-link">
                            Samples
                        </a>

                        <div class="navbar-dropdown">
                            <router-link
                                v-for="(link, index) in menuLinks"
                                :key="index"
                                :to="link[0]"
                                class="navbar-item">
                                {{ link[1] }}
                            </router-link>
                            <hr class="navbar-divider">
                            <a class="navbar-item">
                                Report an issue
                            </a>
                        </div>
                    </div>
                </div>

                <div class="navbar-end">
                    <div class="navbar-item">
                        <div class="buttons-wrapper" v-if="loggedIn">
                            <p class="loggedInEmail">Zalogowany jako <strong>{{ loggedInEmail }}</strong></p>
                            <Button text="Log out" border-color="white" @click.native="logout"></Button>
                        </div>
                        <div class="buttons-wrapper" v-else>
                            <Button text="Sign up" border-color="primary"></Button>
                            <Button text="Log in" border-color="white" @click.native="login"></Button>
                        </div>
                    </div>
                </div>
            </div>
        </nav>
        <div class="managing-panel" v-if="managedUserEmail">
            <p>Zarządzamy <span class="managed-user">{{ this.managedUserEmail }}</span></p>
        </div>
    </div>
</template>

<script>

import Button from '@/components/input/Button.vue';
import { getAuthService } from '../services/authService';
import { ADMIN, USER, SUBUSER } from '../types/role.types';

export default {
    name: 'MainPageHeader',
    components: { Button },
    data() {
        return {
            navbarBurgerExpanded: false,
            loggedInEmail: null,
            role: null,
            managedUserKey: null,
            managedUserEmail: null,
        };
    },
    computed: {
        loggedIn() {
            return this.loggedInEmail != null;
        },

        menuLinks() {
            switch (this.role) {
                case ADMIN:
                    return this.managedUserEmail ? [
                        ['/form', 'Sample'],
                        ['/account-list', 'Lista użytkowników'],
                        [{ name: 'edit-account', params: { account: this.managedUserKey } }, 'Edytuj konto'],
                        ['/tab2', 'Zarządzaj grupą'],
                        ['/tab3', 'Zarządzaj dostępem'],
                    ] : [
                        ['/form', 'Sample'],
                        ['/account-list', 'Lista użytkowników'],
                    ];
                case USER:
                    return [
                        ['/form', 'Sample'],
                        ['/edit-my-account', 'Edytuj konto'],
                        ['/tab2', 'Zarządzaj grupą'],
                        ['/tab3', 'Zarządzaj dostępem'],
                    ];
                case SUBUSER:
                    return [
                        ['/form', 'Sample'],
                        ['/edit-my-account', 'Edytuj konto'],
                        ['/tab2', 'Zarządzaj grupą'],
                        ['/tab3', 'Zarządzaj dostępem'],
                    ];
                default:
                    return [
                        ['/form', 'Sample'],
                    ];
            }
        },
    },
    methods: {
        login() {
            this.$router.push('login');
        },

        async logout() {
            await getAuthService().logOut();
            // Refreshing the page
            this.$router.go();
        },

        expandNavbarBurger() {
            this.navbarBurgerExpanded = !this.navbarBurgerExpanded;
        },
    },
    async mounted() {
        const authService = getAuthService();
        
        [
            this.managedUserKey,
            this.managedUserEmail,
            this.loggedInEmail,
            this.role,
        ] = await Promise.all([
            authService.getManagedUser(),
            authService.getManagedUserEmail(),
            authService.getLoggedInEmail(),
            authService.getRole(),
        ]);
    },
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_variables.scss";
@import "@/assets/scss/bulma/projectBulma.scss";

.main-page-header-wrapper {
    .navbar .navbar-brand .navbar-item.logo img {
        width: 70px;
        height: 50px;
        max-height: 50px;
    }

    .managing-panel {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        background-color: white;

        p {
            padding: 5px 10px;
            border: 1px solid white;
            color: $dark-text;

            span {
                font-weight: bold;
            }
        }
    }

    .loggedInEmail {
        color: $dark-text;
        strong {
            color: white;
        }
    }

    .navbar.is-black .navbar-dropdown {
        background-color: #000;
        color: white;

        .navbar-item:hover, .navbar-item:focus {
            background-color: $primary;
        }
    }

    .buttons-wrapper {
        *:not(:last-child) {
            margin-right: 10px;
        }

        display: inline-flex;
    }
}

</style>
