/**
 * 
 * @param {string} propKey 
 * @returns {[() => string, (value: string) => void]}
 */
const createProp = propKey => [
    () => sessionStorage.getItem(propKey),

    /**
     * @param {string} value
     */
    (value) => {
        if (value != null) {
            sessionStorage.setItem(propKey, value);
        } else {
            sessionStorage.removeItem(propKey);
        }
    },
];

export const [getRole, setRole] = createProp('role');
export const [getLoggedInEmail, setLoggedInEmail] = createProp('loggedInEmail');
export const [getLoggedInKey, setLoggedInKey] = createProp('loggedInKey');
export const [getManagedUser, setManagedUser] = createProp('managedUser');
export const [getManagedUserEmail, setManagedUserEmail] = createProp('managedUserEmail');
